<template>
  <div id="app">
    <button v-for="(c,i) in colors" :key="i" :style="`background: ${c}`" @click="onClick(c)">{{c}}</button>
    <div
      class="product"
      v-for="p in filterProducts"
      :key="p.name"
      :style="`background: ${p.color}`"
    >{{p.name}}</div>
    <pre>Clicks {{count}}</pre>
  </div>
</template>

<script>
export default {
  name: "App",
  data: () => ({
    count: 0,
    colors: ["red", "blue", "yellow", "green"],
    products: [
      {
        id: 1,
        name: "Dress shoes",
        price: 5,
        color: "red",
        compare: null
      },
      {
        id: 2,
        name: "Sports shoes",
        price: 3,
        color: "blue",
        compare: null
      },
      {
        id: 3,
        name: "Beach shoes",
        price: 2,
        color: "yellow",
        compare: null
      },
      {
        id: 4,
        name: "Slippers",
        price: 1,
        color: "green",
        compare: null
      }
    ],
    filterBy: ""
  }),
  computed: {
    filterProducts() {
      if (this.filterBy === "") return this.products;
      else return this.products.filter(p => p.color === this.filterBy);
    }
  },
  methods: {
    onClick(color) {
      this.filterBy = color;
      this.count++;
    }
  }
};
</script>

